/* Обнуление */

*,
*::before,
*::after {
    padding: 0;
    margin: 0;
    border: 0;
    box-sizing: border-box;
}

a {
    text-decoration: none;
}

ul,
ol,
li {
    list-style: none;
}

img {
    vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-weight: inherit;
    font-size: inherit;
}

html,
body {
    height: 100%;
    line-height: 1;
    background-color: #fafafa;
    font-size: 14px;
    color: #252b42;
    font-weight: 500;
    font-family: Montserrat;
}

/*---------------------------------------------------------------------------*/

.wrapper {
    min-height: 100%;
    overflow: hidden;
    display: flex;
    flex-direction: column;
}

/*----------------------------------------------------------------------------*/

._container {
    max-width: 1046px;
    padding: 0 15px;
    margin: 0 auto;
    box-sizing: content-box;
}

._ibg {
    position: relative;
}

._ibg img {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    object-fit: cover;
}

/*-----Шапка---------------------------------------------------------------------------*/

.header {
    position: absolute;
    width: 100%;
    left: 0;
    top: 0;
    z-index: 50;
}

.header__container {
    display: flex;
    min-height: 104px;
    align-items: center;

}

@media (max-width: 479px) {
    .header__container {
        min-height: 70px;
    }
}

.header__logo {
    font-size: 24px;
    font-weight: 700;
    color: inherit;
}

.header__menu {
    margin: 0 0 0 133px;
}

@media (max-width: 992px){
    .header__menu {
        margin: 0 0 0 80px;
    }
}

@media (max-width: 767px){
    .header__menu {
        margin: 0 0 0 40px;
    }
}

@media (max-width: 479px){
    .header__menu {
        margin: 0 0 0 20px;
    }
}

.menu {
    padding: 5px 0;
}

.menu__list {
    display: flex;
    flex-wrap: wrap;
}


.menu__item:not(:last-child) {
    margin: 0 21px 0 0;
}
.menu__link {
    color: #737373;
    font-weight: 600;
    line-height: calc(24 / 14 * 100%);
}

.menu__link:hover {
    text-decoration: underline;
}

@media (max-width: 479px){
    .menu__link {
        font-size: 18px;
    }
}

/*-----PAGE----------------------------------------------------------------------*/

.page {
    flex: 1 1 auto;
}

.page__services {
    position: relative;
    z-index: 2;
    margin: -111px 0px 0px 0px;
}

.page__clients:not(:last-child) {
    margin: 0 0 10px 0;
}


/*-----HEADER-BLOCK------------------------------------------------------------*/

.header-block {
    text-align: center;
    letter-spacing: 0.2px;

}

.header-block__title {
    line-height: 142%;
    font-size: 40px;
    font-weight: 700;
}

.header-block__title:not(:last-child) {
    margin: 0 0 10px 0;
}

.header-block__sub-title {
    line-height: 142%;
    color:#737373;

}

@media (max-width: 992px) {
    .header-block__title {
        font-size: 35px;
    }
}

@media (max-width: 767px) {
    .header-block__title {
        font-size: 30px;
    }
}

/*-----MAIN-BLOCK---------------------------------------------------------------*/

.main-block {
    position: relative;
}


.main__block__body {
    padding: 184px 0 191px 0;
    position: relative;
    z-index: 2;
    max-width: 400px;
}

@media (max-width: 992px) {
    .main__block__body {
        padding: 120px 0 191px 0;
    }
}

@media (max-width: 479px) {
    .main__block__body {
        padding: 115px 0 160px 0;
    }
}

.main__block__body > *:not(:last-child) {
    margin: 0 0 35px 0;
}

.main-block__title {
    font-size: 58px;
    line-height: 137%;
    letter-spacing: 0.2px;

    font-weight: 800;
}

@media (max-width: 479px) {
    .main-block__title {
        font-size: 42px;
    }
}

.main-block__text {
    color: #737373;
    font-size: 20px;
    line-height: 150%;
}

@media (max-width: 479px) {
    .main-block__text {
        color: #585757;
    }
}

.main-block__buttons {
    display: flex;
    flex-wrap: wrap;
    margin: -10px 0;
}

.main-block__button {
    border-radius: 37px;
    display: flex;
    min-height: 48px;
    justify-content: center;
    align-items: center;
    letter-spacing: 0.2px;
    font-weight: 700;
    transition: all 0.3s ease 0s;
    text-align: center;
    line-height: 150%;
    padding: 5px 36px;
    margin: 10px 0;
}

@media (max-width: 479px) {
    .main-block__button {
        flex: 1 1 100%;
    }
}


@media (min-width: 479px) {
    .main-block__button:not(:last-child) {
        margin-right: 10px;
    }
}

.main-block__button_orange {
    background: #ff7b47;
    color: #fff;
}

.main-block__button_orange:hover {
    background-color: #aa5330;
}

.main-block__button_border {
    color: #0d5c63;
    border: 1px solid #0d5c63;
}

.main-block__button_border:hover {
    background-color: #0d5c63;
    color:#fff;
}

.main-block__image {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
}

@media (max-width: 1300px) {
    .main-block__image img {
         object-position: right;
    }
}

@media (max-width: 992px) {
    .main-block__image {
